import { FallOutlined, HistoryOutlined } from "@ant-design/icons";
import { Button, Tooltip } from "antd";
import React from "react";
import { withOnline } from "../../api/online";

function ConnectorButtons(props) {
  let charging = ["preparing", "charging", "finishing"].includes(
    props.statecode
  );
  let reserved = ["reserved"].includes(props.statecode);

  return (
    <React.Fragment>
      <Tooltip title={props.m("btn.charge")}>
        <Button
          className="gx-mr-1"
          onClick={(e) => {
            e.stopPropagation();
            props.chargeToggle(charging);
          }}
          icon={
            <FallOutlined className={`${charging ? "gx-text-warning" : ""}`} />
          }
        />
      </Tooltip>
      <Tooltip title={props.m("btn.reserve")}>
        <Button
          onClick={(e) => {
            e.stopPropagation();
            props.reserveToggle(reserved);
          }}
          icon={
            <HistoryOutlined
              className={`${reserved ? "gx-text-warning" : ""}`}
            />
          }
        />
      </Tooltip>
    </React.Fragment>
  );
}
const mapOnlineProps = {
  statecode: (d) => d.statenewcode,
};
export default withOnline(ConnectorButtons, mapOnlineProps);
