import { Card, Drawer, Empty } from "antd";
import React from "react";
import { Helmet } from "react-helmet";
import { injectIntl } from "react-intl";
import { withRouter } from "react-router-dom";
import { YMaps, Map, Placemark, ZoomControl } from "react-yandex-maps";
import CircularProgress from "../../components/CircularProgress";
import Context from "../../Context";
import CustomScrollbars from "../../util/CustomScrollbars";
import paramsHasChanged from "../../util/paramsHasChanged";
import PlainSelect from "../../util/PlainSelect";
import DriverStationDetail from "./DriverStationDetail";
import StationsMap from "./StationsMap";

class DriverStations extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
	this.config = window.appConfig || { mapLanguage: "ru_RU" };
    this.state = {
		
      mapLoaded: false,
      pending: false,
      stations: [],

      carId: false,
      chargerId: false,

      drawerVisible: false,
      drawerItem: {},
    };
  }

  pendingStart = () => {
    if (this.props.onPendingStart) {
      this.props.onPendingStart();
      return;
    }
    this.setState({ pending: true });
  };

  pendingEnd = () => {
    if (this.props.onPendingEnd) {
      this.props.onPendingEnd();
      return;
    }
    this.setState({ pending: false });
  };

  m = (key, prefix = "stations.map.") => {
    const { intl } = this.props;
    return intl.formatMessage({ id: prefix + key });
  };

  componentDidMount() {
    this.setParams();
    this.load();
  }

  componentDidUpdate(prevProps) {
    if (paramsHasChanged(prevProps, this.props)) {
      this.setParams();
    }
    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.load();
    }
  }

  setParams = () => {
    const { param1: carId, param2: chargerId } = this.props.match.params;
    this.setState(
      {
        carId,
        chargerId,
      },
      () => this.load()
    );
  };

  load = async () => {
    this.pendingStart();

    let search = [];
    if (this.state.chargerId) {
      search.push({
        field: "connectors",
        compare: "GIN",
        value: [{ charger: this.state.chargerId }],
      });
    }
    let stations = await this.context.api.q(
      "/charge_point/list",
      { search },
      { toArray: true }
    );
    this.setState({ stations }, () => this.pendingEnd());
  };

  getItemGeo = (item) => {
    let geo = item?.geo?.[0] || {};
    let lat = geo.latitude;
    let lng = geo.longitude;
    if (lat && lng) {
      return [lat, lng];
    }
    return null;
  };

  drawerClose = () => {
    this.setState({ drawerVisible: false });
  };

  onPlacemarkClick = (drawerItem) => {
    this.setState({ drawerItem, drawerVisible: true });
  };

  render() {
    const { stations, mapLoaded, pending, drawerItem } = this.state;
    let defaultCenter = window.appConfig.mapDefaultCenter;
	if (!Array.isArray(stations)) return null;
    if (stations.length === 0) return <Empty />;

    
      const points = stations
      .map((item) => this.getItemGeo(item))
      .filter((geo) => geo);
	  if (points.length === 0) {
		return (
		  <div className={this.props.className}>
			<Empty />
		  </div>
		);
	  }
  
	  
    const mapRef = React.createRef();
	const lang = this.config.mapLanguage;
    return (
      <>
        {(!mapLoaded || pending) && <CircularProgress />}
        <Drawer
          title={drawerItem?.label}
          placement="right"
          onClose={this.drawerClose}
          visible={this.state.drawerVisible}
        >
          <CustomScrollbars>
            <DriverStationDetail stationId={drawerItem?.id} />
          </CustomScrollbars>
        </Drawer>
        <Helmet>
          <title>{this.m("stations")}</title>
        </Helmet>
        <div
          className="gx-st-map huge"
          style={{ visibility: mapLoaded && !pending ? "visible" : "hidden" }}
        >
          <div className="filterbox">
            <Card size="small">
              <PlainSelect
                value={this.state.carId}
                placeholder={this.m("forcar")}
                formatLabel={(item) => `${item.label} ${item.identity}`}
                extraFields={["identity", "chargerac"]}
                enabledOnly
                labelKey="label"
                query="/car/list"
                onChange={(v, item) => {

                  this.props.history.push(
                    `/driver/stations/${v}/${item.chargerac || ""}`
                  );
                }}
              />
            </Card>
          </div>
          <YMaps key={lang} query={{ lang }}>
            <Map
              defaultState={{ center: points[0] || [50.85045, 4.34878], zoom: 9 }}
              width={"100%"}
              height={"100%"}
              modules={["geoObject.addon.balloon", "util.bounds"]}
              onLoad={(ymaps) => {
                this.setState({ mapLoaded: true });
                if (points.length > 1) {
                  mapRef.current.setBounds(
                    ymaps.util.bounds.fromPoints(points)
                  );
                }
              }}
              instanceRef={(ref) => {
                if (ref) {
                  mapRef.current = ref;
                }
              }}
            >
              {stations.length !== 0 ? stations.map((item, idx) => {
                let geo = this.getItemGeo(item);
                if (!geo) return null;

                let balloonContent = [];
                if (item.description)
                  balloonContent.push(`<div>${item.description || ""}</div>`);
                if (item.modelname)
                  balloonContent.push(
                    `<div>${item.modelname || ""} ${
                      item.vendorname || ""
                    }</div>`
                  );

                return (
                  <Placemark
                    key={idx}
                    defaultGeometry={geo}
                    onClick={() => this.onPlacemarkClick(item)}
                    options={{
                      iconLayout: "default#image",
                      iconImageHref: "/assets/images/images-32-removebg-preview.png",
                      iconImageSize: [32, 32],
                    }}
                  />
                );
              }) : []}
              <ZoomControl />
            </Map>
          </YMaps>
        </div>
      </>
    );
  }
}

export default withRouter(injectIntl(DriverStations));
