import React from "react";
import { Empty, Spin } from "antd";
import { injectIntl } from "react-intl";
import { YMaps, Map, Placemark, ZoomControl } from "react-yandex-maps";

class StationsMap extends React.Component {
  constructor(props) {
    super(props);
    this.config = window.appConfig || { mapLanguage: "ru_RU" };
    this.state = {
      mapLoaded: false,
    };
  }

  m = (key, prefix = "stations.map.") => {
    const { intl } = this.props;
    return intl.formatMessage({ id: prefix + key });
  };

  getItemGeo = (item) => {
    let geo = item?.geo?.[0] || {};
    let lat = geo.latitude;
    let lng = geo.longitude;
    if (lat && lng) {
      return [lat, lng];
    }
    return null;
  };

  render() {
    const { stations } = this.props;
    if (!Array.isArray(stations)) return null;
    if (stations.length === 0) return <Empty />;

    const mapRef = React.createRef();

    const points = stations
      .map((item) => this.getItemGeo(item))
      .filter((geo) => geo);

    if (points.length === 0) {
      return (
        <div className={this.props.className}>
          <Empty />
        </div>
      );
    }

    const lang = this.config.mapLanguage;

    return (
      <div className={this.props.className}>
        {!this.state.mapLoaded && <Spin spinning></Spin>}
        <YMaps key={lang} query={{ lang }} >
          <Map
            defaultState={{ center: points[0] || [], zoom: 9 }}
            width={"100%"}
            height={"100%"}
            modules={["geoObject.addon.balloon", "util.bounds"]}
            onLoad={(ymaps) => {
              this.setState({ mapLoaded: true });
              if (points.length > 1) {
                mapRef.current.setBounds(ymaps.util.bounds.fromPoints(points));
              }
            }}
            instanceRef={(ref) => {
              if (ref) {
                mapRef.current = ref;
              }
            }}
          >
            {stations.map((item, idx) => {
              let geo = this.getItemGeo(item);
              if (!geo) return null;

              let balloonContent = [];
              if (item.description)
                balloonContent.push(`<div>${item.description || ""}</div>`);
              if (item.modelname)
                balloonContent.push(
                  `<div>${item.modelname || ""} ${item.vendorname || ""}</div>`
                );

              return (
                <Placemark
                  key={idx}
                  defaultGeometry={geo}
                  options={{
                    iconLayout: "default#image",
                    iconImageHref: "/assets/images/images-32-removebg-preview.png",
                    iconImageSize: [32, 32],
                  }}
                  properties={{
                    balloonContentHeader: `${item.label}`,
                    balloonContent: balloonContent.join(""),
                    balloonContentFooter: `<a href="/crm/station/${
                      item.id
                    }">${this.m("more")}</a>`,
                  }}
                />
              );
            })}
            <ZoomControl />
          </Map>
        </YMaps>
      </div>
    );
  }
}
export default injectIntl(StationsMap);
