import { message, Popover, Spin, Tag, Modal } from "antd";
import { injectIntl } from "react-intl";
import React from "react";
import Context from "../Context";
import { ExclamationCircleOutlined } from "@ant-design/icons";
import { withOnline } from "../api/online";

const { confirm } = Modal;

class Statelabel extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
    this.state = {
      popoverOpen: false,
      methods: [],
    };
  }

  popoverToggle = () => {
    this.setState(
      (state) => {
        return {
          popoverOpen: !state.popoverOpen,
          methods: !state.popoverOpen ? [] : state.methods,
        };
      },
      () => this.load()
    );
  };

  pendingStart = () => {
    if (this.props.onPendingStart) {
      this.props.onPendingStart();
      return;
    }
    this.setState({ pending: true });
  };

  pendingEnd = () => {
    if (this.props.onPendingEnd) {
      this.props.onPendingEnd();
      return;
    }
    this.setState({ pending: false });
  };

  load = async () => {
    if (this.state.popoverOpen) {
      this.pendingStart();
      let methods = await this.context.api.q(
        `/${this.props.classcode}/method`,
        { id: this.props.id },
        { toArray: true }
      );
      this.setState({ methods }, () => this.pendingEnd());
    }
  };

  run = async (method) => {
    this.pendingStart();
    let result = await this.context.api.q("/method/run", {
      id: this.props.id,
      method: method.id,
    });
    this.pendingEnd();
    this.popoverToggle();
    if (!result) {
      message.error(this.m("method.fail"));
      this.setState({ pending: false });
    } else {
      message.success(this.m("method.success"));
      if (this.props.onFinish) {
        this.props.onFinish(method);
      }
    }
  };

  ask = async (method) => {
    if (method.actioncode === "drop") {
      confirm({
        title: this.m("method.confirm.title"),
        content: this.m("method.confirm.text"),
        icon: <ExclamationCircleOutlined />,
        onOk: () => {
          this.run(method);
        },
      });
    } else {
      this.run(method);
    }
  };

  m = (id) => {
    const { intl } = this.props;
    return intl.formatMessage({ id });
  };

  render() {
    if (!this.props?.statelabel) return null;

    const { pending, methods = [] } = this.state;

    const colors = {
      created: "processing",
      enabled: "success",
      disabled: "default",
      deleted: "error",
    };

    const label = (
      <Tag className="gx-m-0" color={colors[this.props.statetypecode]}>
        {this.props.statelabel}
      </Tag>
    );

    if (this.props.readOnly) return label;

    return (
      <Popover
        content={
          <div className={`${methods.length === 0 && "gx-p-3"}`}>
            <Spin spinning={pending}>
              {methods.map((method) => {
                if (!method.visible) return null;
                return (
                  <div
                    key={method.id}
                    onClick={() => this.ask(method)}
                    className="gx-my-2 gx-pointer gx-link"
                  >
                    {method.label}
                  </div>
                );
              })}
            </Spin>
          </div>
        }
        trigger="click"
        visible={this.state.popoverOpen}
        onVisibleChange={this.popoverToggle}
      >
        <div className="gx-pointer">{label}</div>
      </Popover>
    );
  }
}

const mapOnlineProps = {
  statelabel: (d) => d.statenewlabel,
  statetypecode: (d) => d.statenewtypecode,
};
export default withOnline(injectIntl(Statelabel), mapOnlineProps);
