import React from "react";
import { injectIntl } from "react-intl";
import { Select } from "antd";
import Context from "../Context";

const { Option } = Select;

class PlainSelect extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
    this.state = {
      searchText: "",
      pending: false,
      items: [],
    };
  }
  componentDidMount() {
    this.load().then(this.shureHaveValue());
  }

  componentDidUpdate(prevProps) {
    if (prevProps.value !== this.props.value) {
      this.shureHaveValue();
    }
  }

  createSearch = () => {
    let search = this.props.search || [];
    if (this.state.searchText && this.props.async) {
      search.push({
        field: this.props.labelKey || "label",
        compare: "IKE",
        value: `%${this.state.searchText}%`,
      });
    }
    if (this.props.enabledOnly) {
      search.push({
        field: "statetypecode",
        compare: "EQL",
        value: "enabled",
      });
    }
    return search;
  };

  getLabelKey = () => this.props.labelKey || "label";

  getIdKey = () => this.props.valueKey || "id";

  shureHaveValue = async () => {
    if (this.props.async) {
      if (
        !this.state.items.find(
          (item) => item[this.getIdKey()] === this.props.value
        )
      ) {
        this.setState({ pending: true });
        let result = await this.context.api.q("/object/get", {
          id: this.props.value,
          fields: [this.getIdKey(), this.getLabelKey()],
        });
        this.setState({ pending: false });
        if (result?.[this.getLabelKey()]) {
          this.setState(({ items }) => {
            items.push(result);
            return { items };
          });
        }
      }
    }
  };

  load = async () => {
    if (this.props.items) {
      this.setState({ items: this.props.items });
      return;
    }

    let items = [];

    let reclimit = this.props.async ? 1 : 0;

    if (this.props.query) {
      this.setState({ pending: true });
      let search = this.createSearch();

      let fields = [this.getIdKey(), this.getLabelKey()].concat(
        this.props.extraFields || []
      );

      let fetchFn = this.props.cache
        ? this.context.api.cache
        : this.context.api.q;
      items = await fetchFn(
        this.props.query,
        { search, reclimit, fields },
        { toArray: true }
      );
      this.setState({ pending: false, items });
    }
  };
  onSearch = async (value) => {
    if (this.props.async) {
      this.setState({ searchText: value }, () => this.load());
    }
  };

  onChange = (value) => {
    let item = this.state.items.find((i) => i[this.getIdKey()] === value);
    this.props.onChange(value, item);
  };

  render() {
    let valueRendered = false;
    let items = this.state.items.map((item, idx) => {
      let option = item || {};
      let value = option[this.getIdKey()];
      let label = this.props.formatLabel
        ? this.props.formatLabel(item)
        : option[this.getLabelKey()];
      if (value === this.props.value) valueRendered = true;
      return (
        <Option key={idx} value={value}>
          {label}
        </Option>
      );
    });

    return (
      <Select
        defaultActiveFirstOption={false}
        allowClear={this.props.allowClear}
        loading={this.state.pending ? true : false}
        value={!this.state.pending ? this.props.value : null}
        showSearch
        placeholder={this.props.placeholder}
        optionFilterProp="children"
        onChange={this.onChange}
        onSearch={this.onSearch}
        filterOption={(input, option) =>
          option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
        }
      >
        {items}
      </Select>
    );
  }
}
export default injectIntl(PlainSelect);
