import React from "react";
import Context from "../../Context";
import { Link, withRouter } from "react-router-dom";
import { injectIntl } from "react-intl";
import { Button, Col, Empty, message, Row, Spin, Tooltip } from "antd";
import isDate from "../../util/isDate";
import Flex from "../../util/Flex";
import Statelabel from "../../util/Statelabel";
import ConnectorButtons from "../Connectors/ConnectorButtons";
import { online } from "../../api/online";
//import config from "../../api/config";

// if (!window.onlineInterval && config.apiCheckInterval) {
//   window.setInterval(() => online.tick(), config.apiCheckInterval);
// }

online.tick();

class DriverStationDetail extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);

    this.state = {
      detailPending: false,
      pending: false,
      stationId: false,
      item: {},
      access: {},
      files: [],
    };
  }
  m = (key, prefix = "stations.") => {
    const { intl } = this.props;
    return intl.formatMessage({ id: prefix + key });
  };

  pendingStart = () => {
    if (this.props.onPendingStart) {
      this.props.onPendingStart();
      return;
    }
    this.setState({ pending: true });
  };
  pendingEnd = () => {
    if (this.props.onPendingEnd) {
      this.props.onPendingEnd();
      return;
    }
    this.setState({ pending: false });
  };

  componentDidMount() {
    this.load();
  }

  componentDidUpdate(prevProps) {
    if (
      prevProps.stationId !== this.props.stationId ||
      prevProps.intl.locale !== this.props.intl.locale
    ) {
      this.setState({ item: {} }, () => this.load());
    }
  }

  load = async () => {
    if (this.props.stationId) {
      this.pendingStart();

      let readings = [
        this.context.api.q("/charge_point/get", {
          id: this.props.stationId,
        }),
        this.context.api.q("/object/access/decode", {
          id: this.props.stationId,
        }),
        this.context.api.q(
          "/object/file",
          {
            id: this.props.stationId,
          },
          { toArray: true }
        ),
      ];
      let [item, access, files] = (await Promise.allSettled(readings)).map(
        (result) => result.value
      );

      this.setState({ item, access, files }, () => this.pendingEnd());
    } else {
      this.setState({ item: {}, access: {}, files: [] });
    }
  };

  chargeToggle = async (item = {}) => {
    if (item.id) {
      this.setState({ detailPending: true });
      let result = false;
      if (item.charging) {
        result = await this.context.api.q("/connector/remote/stop", {
          id: item.id,
        });
      } else {
        result = await this.context.api.q("/connector/remote/start", {
          id: item.id,
        });
      }
      this.setState({ detailPending: false });
      if (result === false) {
        message.error(this.m("method.fail", ""));
      } else {
        message.success(this.m("method.success", ""));
      }
      this.load();
    }
  };

  reserveToggle = async (item = {}) => {
    if (item.id) {
      this.setState({ detailPending: true });
      let result = false;
      if (item.reserved) {
        result = await this.context.api.q("/connector/reserve/cancel", {
          id: item.id,
        });
      } else {
        result = await this.context.api.q("/connector/reserve/now", {
          id: item.id,
        });
      }
      this.setState({ detailPending: false });
      if (result === false) {
        message.error(this.m("method.fail", ""));
      } else {
        message.success(this.m("method.success", ""));
      }
      this.load();
    }
  };

  render() {
    const { pending, item, access, files } = this.state;
    const connectors = item.connectors || [];

    const displayDetailProps = [
      "identity",
      "modelname",
      "vendorname",
      "serialnumber",
      "boxserialnumber",
      "iccid",
      "imsi",
      "networkname",
      "connected",
      "connectionupdate",
      "lastupdate",
    ];

    const DetailRow = ({item, property, connected}) => {
      let value = item?.[property];
      if (typeof value === "string") {
        if (property === 'lastupdate' || property === 'created' || property === 'connectionupdate') {
          value = new Date(value).toLocaleString();
        }
      } else if (typeof value === "boolean") {
        value = value ? this.m("true") : this.m("false")
      }
      if (!value) return null;
      return (
        <Row gutter={[16, 16]} className="gx-mb-2">
          <Col className="gx-text-muted gx-font-weight-light gx-pr-2" xs={10}>
            {property === 'connectionupdate' ? connected ? this.m(property) : this.m("disconnected") : this.m(property)}
          </Col>
          <Col xs={14}>{value}</Col>
        </Row>
      );
    };

    return (
      <div>
        <Spin spinning={pending}>
          {!item?.id && <Empty />}
          {item?.id && (
            <div className="gx-mb-3">
              {item?.description && (
                <div className="gx-text-muted gx-font-weight-light gx-mb-3">
                  {item.description}
                </div>
              )}
              <Flex between className="gx-mb-3 gx-pr-1">
                <h4>{this.m("details")}</h4>
                <Statelabel
                  {...item}
                  readOnly={!access?.u}
                  onFinish={this.load}
                />
              </Flex>
              {displayDetailProps.map((prop) => (
                <DetailRow key={prop} item={item} property={prop} connected={item.connected} />
              ))}
              {connectors.length > 0 && (
                <>
                  <Spin spinning={this.state.detailPending}>
                    <hr />
                    {connectors.map((c, idx) => {
                      return (
                        <div className="gx-mb-2" key={idx}>
                          <Flex between>
                            <div>
                              {c.chargername}
                              <div className="gx-font-weight-light gx-text-small gx-text-muted">
                                {c.chargerdescription}
                              </div>
                            </div>
                            <div className="gx-ml-2 gx-mr-1">
                              <Statelabel
                                {...c}
                                readOnly={true}
                                onFinish={this.load}
                              />
                              <div style={{ whiteSpace: "nowrap" }}>
                                <ConnectorButtons
                                  m={this.m}
                                  {...c}
                                  chargeToggle={(value) =>
                                    this.chargeToggle(c, value)
                                  }
                                  reserveToggle={(value) =>
                                    this.reserveToggle(c, value)
                                  }
                                />
                              </div>
                            </div>
                          </Flex>
                        </div>
                      );
                    })}
                  </Spin>
                </>
              )}
              {access?.u && (
                <Link to={`/crm/station/${item.id}`}>
                  <Button className="gx-mb-0 gx-mt-4">
                    {this.m("edit", "")}
                  </Button>
                </Link>
              )}
              {files.length > 0 && (
                <>
                  <hr />
                  {files.map((f, idx) => {
                    return (
                      <img
                        key={idx}
                        alt=""
                        src={window.appConfig.apiDomain + f.link}
                      />
                    );
                  })}
                </>
              )}
            </div>
          )}
        </Spin>
      </div>
    );
  }
}

export default withRouter(injectIntl(DriverStationDetail));
